import { CSSProperties } from "react";

import {
	DropdownMenuContentProps,
	DropdownMenuItemProps as RadixDropDownMenuItemProps,
	DropdownMenuPortalProps,
	DropdownMenuProps as RadixDropDownMenuProps,
} from "@radix-ui/react-dropdown-menu";

import { colors } from "../styleguide";

export type SidePosition = "top" | "bottom";
export type AlignPosition = "start" | "center" | "end";

export interface StyledDropDownMenuProps
	extends Omit<DropdownMenuContentProps, "side" | "align"> {
	side?: SidePosition;
	align?: AlignPosition;
	$menuWidth?: string;
	$menuHeight?: string;
	$zIndex?: CSSProperties["zIndex"];
}

export interface DropDownMenuProps extends StyledDropDownMenuProps {
	dataQA: string;
	ariaLabel: string;
	className?: string;
}

export interface DropDownProps
	extends DropdownMenuPortalProps,
		DropDownMenuProps {
	className?: string;
	trigger?: React.ReactNode;
	onOpenChangeAction?: (open: boolean) => void;
	radixDropdownMenuProps?: RadixDropDownMenuProps;
	isModal?: boolean;
	isTriggerDisabled?: boolean;
}

export interface StyledDropDownMenuItemProps
	extends RadixDropDownMenuItemProps {
	$isDisabled?: boolean;
	$isSelected?: boolean;
	color?: keyof typeof colors | string;
}

export interface DropDownMenuItemProps extends StyledDropDownMenuItemProps {
	className?: string;
}
